import React, { useEffect, useState } from 'react';
import Form from 'react-bootstrap/Form';
import FormGroup from 'react-bootstrap/FormGroup';
import Button from 'react-bootstrap/Button';
import axios from 'axios';

import { useSelector } from 'react-redux';

import { Link, useNavigate } from 'react-router-dom';
import { apiUrl } from '../utils/ApiConfig';
import HomeCard from './HomeCard';

function Test() {
  const { userInfo } = useSelector((state) => state.user);
  const [followingProduct, setFollowingProduct] = useState([]);
  const navigate = useNavigate();
  const [followingSection, setFollowingSection] = useState({});

  useEffect(() => {
    const fetchUserFollowingProducts = async () => {
      try {
        const { data } = await axios.get(
          `${apiUrl}/api/products/${userInfo._id}/following`
        );
        console.log('following', data);
        setFollowingProduct(data);
      } catch (error) {
        console.log(error);
      }
    };
    fetchUserFollowingProducts();
  }, [userInfo._id]);

  const eachFollowingProductAndUser = followingProduct?.map((product) => {
    const products = product?.product?.map((item) => item);
    const productUser = product.user;

    return {
      product: products,
      user: productUser,
    };
  });

  const renderProductsBySection = (sectionKey, userProducts) => {
    switch (sectionKey) {
      case 'newarrival':
        return userProducts
          .filter((product) => {
            const createdAt = new Date(product?.createdAt);
            const sevenDaysAgo = new Date();
            sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7); // Adjust the number of days as needed
            return createdAt >= sevenDaysAgo;
          })
          .map((product) => (
            <HomeCard key={product._id} product={product} />
          ));
      case 'onsale':
        return userProducts
          .filter((product) => product.countInStock > 0)
          .map((product) => (
            <HomeCard key={product._id} product={product} />
          ));
      case 'topselling':
        return userProducts
          .filter((product) => product?.succcessfulPaid > 1)
          .map((product) => (
            <HomeCard key={product._id} product={product} />
          ));
      default:
        return userProducts.map((product) => (
          <HomeCard key={product._id} product={product} />
        ));
    }
  };

  return (
    <div style={{ width: '100%' }}>
      <div>
        <h4>All My Favorite Stores</h4>
      </div>
      {followingProduct.length > 0 ? (
        <div>
          {eachFollowingProductAndUser?.map((item) => (
            <div key={item.user._id} style={{ width: '100%' }}>
              <div className="d-md-flex d-none flex-wrap p-3 colBG mb-3">
                <div style={{ width: '30%', borderRight: '1px solid grey' }}>
                  <h5 className="mb-4">{item?.user?.storeName} </h5>
                  <Link
                    style={{ color: 'inherit' }}
                    className="text-decoration-none"
                    to={`/seller/${item?.user?._id}`}
                  >
                    <Button
                      variant="light"
                      style={{ border: '1px solid black' }}
                    >
                      Visit store
                    </Button>
                  </Link>
                </div>
                <div
                  className="p-2"
                  style={{
                    width: '70%',
                    margin: 'auto',
                  }}
                >
                  <div
                    className="mb-2"
                    style={{ fontSize: '10px', borderBottom: '2px solid grey' }}
                  >
                    <Button
                      className={`${
                        followingSection[item.user._id] === 'newarrival'
                          ? 'followingButtonBg'
                          : ''
                      }`}
                      onClick={() =>
                        setFollowingSection({
                          ...followingSection,
                          [item.user._id]: 'newarrival',
                        })
                      }
                      variant="light"
                    >
                      New Arrivals (0)
                    </Button>
                    <Button
                      className={`${
                        followingSection[item.user._id] === 'onsale'
                          ? 'followingButtonBg'
                          : ''
                      }`}
                      onClick={() =>
                        setFollowingSection({
                          ...followingSection,
                          [item.user._id]: 'onsale',
                        })
                      }
                      variant="light"
                    >
                      On Sale (0)
                    </Button>
                    <Button
                      className={`${
                        followingSection[item.user._id] === 'topselling'
                          ? 'followingButtonBg'
                          : ''
                      }`}
                      onClick={() =>
                        setFollowingSection({
                          ...followingSection,
                          [item.user._id]: 'topselling',
                        })
                      }
                      variant="light"
                    >
                      Top Selling (0)
                    </Button>
                  </div>
                  <div>
                    <div className="d-flex align-items-center flex-wrap">
                      {renderProductsBySection(
                        followingSection[item.user._id] || 'newarrival',
                        item.product
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div>
          {/* ... (no following products) */}
        </div>
      )}
    </div>
  );
}

export default Test;
