import React from 'react';
import Button from 'react-bootstrap/Button';
import SnowshoeingIcon from '@mui/icons-material/Snowshoeing';

function WishList() {
  return (
    <div>
      <div>
        <div className="colBG d-flex align-items-center gap-3 p-3 mb-3">
          <strong>All items (0)</strong>
          <span>My list (0)</span>
        </div>
        <div
          className="colBG d-flex align-items-center justify-content-center"
          style={{ height: '70vh' }}
        >
          <div className='d-flex flex-column gap-3'>
            <div >
              <SnowshoeingIcon style={{width:"100px", height:"100px"}} className='wishListIcon'/>
            </div>
            <strong> It is empty here</strong>
            <Button>start shopping</Button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default WishList;
