import React, { useEffect, useState } from 'react';
import MenuIcon from '@mui/icons-material/Menu';
import FavoriteIcon from '@mui/icons-material/Favorite';
import Form from 'react-bootstrap/Form';
import Badge from 'react-bootstrap/Badge';
import InputGroup from 'react-bootstrap/InputGroup';
import SearchOutlinedIcon from '@mui/icons-material/SearchOutlined';

import { Link, useNavigate } from 'react-router-dom';
import './navigation.css';
import { useDispatch, useSelector } from 'react-redux';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import AddPhotoAlternateIcon from '@mui/icons-material/AddPhotoAlternate';
import Upload from '../components/Upload';
import { logOut } from '../redux/userSection/userSlice';
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import { paymentReset } from '../redux/paymentSection/paymentSlice';
import { userProductReset } from '../redux/userSection/singleProductSlice';
import { productReset } from '../redux/userSection/userProducts';
import { shippingReset } from '../redux/shippingSection/shippinSlice';
import { cartReset } from '../redux/productSection/productSlice';
import { toast } from 'react-toastify';

import axios from 'axios';
import { apiUrl } from '../utils/ApiConfig';
import Category from '../components/Category';

function NaviBar(props) {
  const { userInfo } = useSelector((state) => state.user);
  const {
    cart: { cartItems },
  } = useSelector((state) => state.cart);
  const { open, setOpen, random, setRandom, smallNav,setOpenSections, handleSection, openSection } = props;
  const [isOpen, setIsOpen] = useState(false);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [catOpen, setCatOpen] = useState(false);
  const [categories, setCategories] = useState([]);

  const LogoutHandler = () => {
    dispatch(logOut());
    dispatch(paymentReset());
    dispatch(userProductReset());
    dispatch(productReset());
    dispatch(shippingReset());
    dispatch(cartReset());
    localStorage.removeItem('openSection');

    toast.success('Signed Out', {
      toastId: 'unique-toast-id',
      autoClose: 3000,
    });
  };

  useEffect(() => {
    const fetchCategory = async () => {
      try {
        const { data } = await axios.get(`${apiUrl}/api/products/categories`);
        setCategories(data);
      } catch (error) {
        toast.error('error', { toastId: 'unique-toast-id' });
      }
    };
    fetchCategory();
  }, []);

  try {
  } catch (error) {
    toast.error('error ', {
      toastId: 'unique-toast-id',
      autoClose: 500,
    });
  }

  const handleCategoryMenu = () => {
    setCatOpen(!catOpen);
  };
  const handleCloseOpen = (section) => {
    setIsOpen(!isOpen);
    setOpenSections(section)
  };
  return (
    <div style={{ width: '97vw', margin: 'auto', position: 'relative' }}>
      <div className="d-flex align-items-center justify-content-between mt-3 mx-2">
        <div className="d-flex align-items-center gap-3 ">
          <MenuIcon onClick={handleCategoryMenu} className="buttonDiv" />
          <strong>
            <Link to="/" className="text-decoration-none d-md-none">
              AloServices
            </Link>
          </strong>
        </div>
        <div>
          <Link to="/">
            <h2 className="d-none d-md-flex code">AloServices</h2>
          </Link>
        </div>

        <Link
          className="text-decoration-none"
          to="/products/cart"
          style={{ position: 'relative', color: 'inherit' }}
        >
          <ShoppingCartOutlinedIcon
            style={{ color: 'orange', height: '40px', width: '40px' }}
          />
          {cartItems && (
            <Badge
              className="p-1"
              style={{
                position: 'absolute',
                borderRadius: '20px',
                top: -5,
                right: -2,
              }}
              bg="danger"
            >
              {cartItems?.reduce((a, c) => a + c.quantity, 0)}
            </Badge>
          )}
        </Link>

        {userInfo ? (
          <Link to={`/user/profile/${userInfo?._id}`} className="d-md-none">
            <AccountCircleIcon className="buttonDiv" />
          </Link>
        ) : (
          <Link to={`/login`} className="d-md-none">
            <AccountCircleIcon className="buttonDiv" />
          </Link>
        )}

        <div className=" d-none d-md-flex align-items-center ">
          <FavoriteIcon className="buttonDiv iconLove" />
          <div className="mx-2 " onClick={() => setIsOpen(true)}>
            {' '}
            {userInfo?.imgUrl ? (
              <img src={userInfo?.imgUrl} alt={''} />
            ) : (
              <Link>
                <AccountCircleIcon className="buttonDiv" />
              </Link>
            )}
          </div>
        </div>
      </div>

      <div className="d-flex d-md-none align-items-center mb-1 ">
        <InputGroup
          style={{ borderRadius: ' 20px ' }}
          size="sm"
          className="d-flex serchsmallBg1  "
        >
          <InputGroup.Text
            id="inputGroup-sizing-sm"
            className="iconSearchBorder"
          >
            <SearchOutlinedIcon className="largeSearcIcon " />
          </InputGroup.Text>

          <Form.Control
            aria-label="Small"
            aria-describedby="inputGroup-sizing-sm"
            placeholder="search products..."
            className="serchsmallBg"
          />
        </InputGroup>
      </div>

      <div className="largeSearch m-1 d-md-flex d-none">
        <InputGroup size="lg" className="" style={{ height: '100%' }}>
          <InputGroup.Text id="inputGroup-sizing-sm" style={{ height: '100%' }}>
            <SearchOutlinedIcon className="largeSearcIcon" />
          </InputGroup.Text>
          <Form.Control
            style={{ height: '100%' }}
            aria-label="Small"
            aria-describedby="inputGroup-sizing-sm"
            placeholder="search products..."
            className="bagroundSearch"
          />
        </InputGroup>
      </div>
      {smallNav && (
        <div
          className="d-flex flex-wrap gap-2 align-items-center  d-md-flex d-none justify-content-between p-3 "
          style={{ margin: 'auto', width: '85%', fontSize: '12px' }}
        >
          <Link className="links">Sale</Link>
          <Link className="links">New Arivals</Link>
          <Link className="links">Mens Wear</Link>
          <Link className="links">Womens wear</Link>
          <Link className="links">Bags</Link>
          <Link className="links">Shoes</Link>
          <Link className="links">Sale</Link>
          <Link className="links">New Arivals</Link>
          <Link className="links">Mens Wear</Link>
          <Link className="links">Womens wear</Link>
          <Link className="links">Bags</Link>
          <Link className="links">Shoes</Link>
        </div>
      )}

      {open && (
        <div className="uploadComponent ">
          <Upload
            open={open}
            random={random}
            setRandom={setRandom}
            setOpen={setOpen}
          />
        </div>
      )}
      {isOpen && (
        <div
          style={{
            position: 'absolute',
            top: 0,
            right: 0,
            width: '300px',
            zIndex: '1',
            borderRadius: '5px',
          }}
          className="homeBackGround borderDropdow p-4"
        >
          <div className="text-end">
            <span
              onClick={() => setIsOpen(false)}
              className="fw-bold fs-5 dropDownCancel"
            >
              {' '}
              X
            </span>
          </div>
          <div className="d-flex flex-column ">
            {userInfo ? (
              <>
                <Link
                  onClick={() => setIsOpen(false)}
                  className="text-black text-decoration-none NavdropLink"
                  to={`/user/profile/${userInfo?._id}`}
                >
                  {' '}
                  <strong className=" fw-bold">My Account</strong>
                </Link>
                <Link
                  className="text-black text-decoration-none NavdropLink"
                  onClick={LogoutHandler}
                >
                  Sign Out
                </Link>
                <Link to={`/seller/${userInfo?._id}`} className="text-black text-decoration-none NavdropLink">
                  My AloServices
                </Link>
                <Link onClick={()=> handleCloseOpen("order")} to={`/user/profile/${userInfo._id} `   } className={openSection === "order" ? "sectionStyle text-black text-decoration-none": "text-black text-decoration-none NavdropLink"}>
                  My Orders
                </Link> 
              </>
            ) : (
              <Link
                onClick={() => setIsOpen(false)}
                to="/login"
                className="text-black text-decoration-none NavdropLink"
              >
                Sign In
              </Link>
            )}

            <Link className="text-black text-decoration-none NavdropLink">
              Message Center
            </Link>
            <Link className="text-black text-decoration-none NavdropLink">
              Wishlist{' '}
            </Link>
            <Link className="text-black text-decoration-none NavdropLink">
              My Favourite Stores
            </Link>
            <Link className="text-black text-decoration-none NavdropLink">
              My Coupon
            </Link>
          </div>
        </div>
      )}
      {catOpen && (
        <div
          className={catOpen ? 'categoryButton1' : 'categoryButton2'}
          onClick={() => setCatOpen(false)}
        >
          <div style={{ width: '300px' }} className="trans">
            <Category setCatOpen={setCatOpen} categories={categories} />
          </div>
        </div>
      )}
    </div>
  );
}

export default NaviBar;
